<template>
  <div>
    <!--Form 复合型输入框，选择器+输入框-->
    <el-input placeholder="请输入搜索内容" v-model="inputSearch" class="input-with-select">
      <!--选择器-->
      <!--<el-select v-model="select" slot="prepend" placeholder="请选择">-->
        <!--<el-option label="药品ID" value="1"></el-option>-->
        <!--<el-option label="药品名" value="2"></el-option>-->
      <!--</el-select>-->
      <!--按钮-->
      <!--slot=append是用来把按钮附着在输入框后-->
      <!--<el-button slot="append"  @click="searchMedicine(inputSearch)" icon="el-icon-search"></el-button>-->
    </el-input>
    <!--药品表-->
    <!--ref属性只是个标识，用来指代这个table
    在有多个表格时应该有助于区分-->
    <div align="center">
      <el-table
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        ref="myTable"
        :data="tables.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @selection-change="handleSelectionChange">
        <!--reserve-selection="true"，可以保留选择状态-->
        <el-table-column
          type="selection"
          reserve-selection>
        </el-table-column>
        <el-table-column
          label="药品ID">
          <template slot-scope="scope">{{ scope.row.id }}</template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="药品名">
        </el-table-column>
        <el-table-column
          prop="description"
          label="描述"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="price"
          label="单价">
        </el-table-column>
        <el-table-column label="请领数量">
          <!--一列的计数器分别绑定表格数据,scope.row指的是
          当前行数据对象-->
          <template slot-scope="scope">
            <el-input-number
              size="mini"
              v-model="scope.row.applyNums"
              controls-position="right"
              @change="handleChange()"
              :min="0"
              :max="200">
            </el-input-number>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--分页器 如何保留选中状态-->
    <el-pagination
      small
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size= "pagesize"
      layout="prev, pager, next, total, jumper"
      :total="drugTable.length">
    </el-pagination>
    <!--<el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>-->
    <el-button @click="handleSubmmit()">提交申请</el-button>
      <!--嵌套对话框-->
      <el-dialog title="收货地址" :visible.sync="dialogTableVisible">
        <el-table :data="multipleSelection">
          <el-table-column property="id" label="药品ID" width="150"></el-table-column>
          <el-table-column property="name" label="药品名" width="200"></el-table-column>
          <el-table-column property="applyNums" label="申请数量" width="200"></el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogTableVisible = false">取 消</el-button>
          <!--一个事件绑定多个函数-->
          <el-button type="primary" @click="handleApply();toggleSelection()">确认申请</el-button>
        </span>
      </el-dialog>

    <el-button @click="toggleSelection()">取消选择</el-button>
    <el-button @click="jumpBack()">返回</el-button>
  </div>
</template>

<script>
export default {
  name: 'ApplyMedicine',
  data() {
    return {
      // 没有这一行不会显示已经选择的
      // 可以设为el-select的value属性值
      // 就会默认显示对应的label
      select: '',
      // 让输入框默认是空的
      inputSearch: '',
      // 页面默认位置
      currentPage: 1,
      // 一个页面的数据数
      pagesize:5,
      // total:0,
      tableData: [
        {
          id: '01',
          price: '20.0',
          description: '色泽鲜艳,有剧毒',
          name: '张三用',
          applyNums: '0'
        },
        {
          id: '02',
          price: '250.0',
          description: '真香',
          name: 'wjz',
          applyNums: '0'
        },
        {
          id: '03',
          price: '30.0',
          description: '未知',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '04',
          price: '40.0',
          description: '未知',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '05',
          price: '50.0',
          description: '未知',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '06',
          price: '60.0',
          description: '未知',
          name: 'xxx',
          applyNums: '0'
        }
      ],
      // 记录了选择的表项
      multipleSelection: [],
      //数据表
      drugTable:[
        // {
        //   id: '01',
        //   price: '20.0',
        //   description: '色泽鲜艳,有剧毒',
        //   name: '张三用',
        //   applyNums: '0'
        // },
        // {
        //   id: '02',
        //   price: '250.0',
        //   description: '真香',
        //   name: 'wjz',
        //   applyNums: '0'
        // },
        // {
        //   id: '03',
        //   price: '30.0',
        //   description: '未知',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '04',
        //   price: '40.0',
        //   description: '未知',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '05',
        //   price: '50.0',
        //   description: '未知',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '06',
        //   price: '60.0',
        //   description: '未知',
        //   name: 'xxx',
        //   applyNums: '0'
        // }
      ],
      applyTable:[],
      //数据加载
      loading:true,
      dialogTableVisible:false
    }
  },
  computed:{
      // 实现搜索的逻辑
      tables (){
        const search = this.inputSearch
        if (search) {
          // filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
          // 注意： filter() 不会对空数组进行检测。
          // 注意： filter() 不会改变原始数组。
          return this.drugTable.filter(data => {
            // some() 方法用于检测数组中的元素是否满足指定条件;
            // some() 方法会依次执行数组的每个元素：
            // 如果有一个元素满足条件，则表达式返回true , 剩余的元素不会再执行检测;
            // 如果没有满足条件的元素，则返回false。
            // 注意： some() 不会对空数组进行检测。
            // 注意： some() 不会改变原始数组。
            return Object.keys(data).some(key => {
              // indexOf() 返回某个指定的字符在某个字符串中首次出现的位置，如果没有找到就返回-1；
              // 该方法对大小写敏感！所以之前需要toLowerCase()方法将所有查询到内容变为小写。
              return String(data[key]).toLowerCase().indexOf(search) > -1
            })
          })
        }
        return this.drugTable
      }
  },
  methods: {
    // 表格的勾选 @selection-change绑定
    // element-ui 实现的，原理未知，传的形参是
    // 行对象加一些其他数据
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },

    //计数器变化时
    handleChange(){
      //
    },
    // 更改单页面大小的附加操作 用@size-change绑定
    handleSizeChange (val) {
      this.pagesize = val;
      console.log(`每页 ${val} 条`);
    },

    // 更改显示页面的附加操作 用@current-change绑定
    handleCurrentChange: function() {
      // this.currentPage = val;
      console.log(`当前页:${this.currentPage}`);
      // console.log(`当前页: ${val}`);
      // return this.$axios.get('/allocateCtrl/applyMedicine',{
      //   params:{
      //     index:index
      //   }
      // })
      // .then((res) => {
      //   let data = res.data.data
      //   let status = res.data.status
      //   if (status === 'success') {
      //     this.drugTable = data
      //     // this.total = drugTable.length()
      //     this.total=20
      //   } else if (status === 'fail') {
      //     this.$message.error('获取数据失败')
      //   }
      // })
      // .catch((error) => {
      //   this.$message.error(error.toString())
      // })
    },

    // 返回按钮
    jumpBack(){
      this.$router.push({path: '/allocateCtrl'})
    },

    // 提交按钮
    handleSubmmit () {
      if(this.multipleSelection.length != 0)
      {
        for(var i in this.multipleSelection){
          if(this.multipleSelection[i].applyNums === 0){
            this.$message({
              showClose: true,
              message: '有药品申请数量为0，无法提交',
              type: 'error'
            });
            break;
          }
          this.dialogTableVisible = true;
        }
      }
      else{
        this.$message({
          showClose: true,
          message: '未选择任何药品，无法提交',
          type: 'error'
        });
      }
    },

    // 确认申请信息，Others-Dialog嵌套对话框
    handleApply(){
      this.dialogTableVisible = false;
      // 只提取对象数组中的特定属性，map函数，注意返回的是键值对
      // console中打印了drugTable也是有一些乱七八糟的函数
      // 说明生成的applyTable是没有这些东西的,查了下好像是vue的底层的东西
      // http://hcysun.me/vue-design/art/7vue-reactive.html#observer-构造函数
      // JSON.stringfy 把对象数组变成json字符串，然后在服务端转化回去
      // 属性名要和接收的VO一致，不然转换的对象属性缺失
      this.applyTable = JSON.stringify(
        this.multipleSelection.map(item =>{
          return {drugId:item.id, num:item.applyNums}
        })
      );
      console.log(this.applyTable);
      return this.$axios.post('/allocateCtrl/applyMedicine',{
        applyTable: this.applyTable
      })
      .then((res) => {
        let status = res.data.status;
        console.log(status);
        if (status === 'success') {
          this.$message({
            showClose: true,
            message: '申请成功',
            type: 'success'
          })
        } else if (status === 'refused') {
          this.$message.error('申请失败')
        }
      })
      .catch((error) => {
        console.log("进入错误处理");
        // TypeError: Cannot read property '$message' of undefined
        this.$message.error("lyfNB")
      })
    },
    // 取消选择按钮
    // this.$ref.myTable使用了设置的标识
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.myTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.myTable.clearSelection();
      }
      //清空计数器
      for(var i in this.drugTable){
        this.drugTable[i].applyNums = 0;
      }
    }
  },

  // 昨晚浪费时间点1，写到method里面了
  // 和后端的交互

  beforeMount:function () {
    return this.$axios.get('/allocateCtrl/showDrugs',{
      params:{
        index: 1
      }
    })
      .then((res) => {
        //服务端发来的数据时res.data，res里还有别的
        let data = res.data.data
        let status = res.data.status
        if (status === 'success') {
          this.drugTable = data
          this.total = data.length
          this.loading = false
        } else if (status === 'fail') {
          this.$message.error('获取数据失败')
        }
      })
      .catch((error) => {
        this.$message.error(error.toString())
      })
  }
}
</script>

<style scoped>
  /*搜索框的样式*/
  .el-input{
    width: 500px;
  }
  .el-select{
    width: 100px;
  }
  /*数据表的样式*/
  .el-table{
    width: 800px;
    height: 400px;
  }
</style>



