<template>
  <div>
    <!--Form 复合型输入框，选择器+输入框-->
    <el-input placeholder="请输入搜索内容" v-model="inputSearch" class="input-with-select">
      <!--选择器-->
      <!--<el-select v-model="select" slot="prepend" placeholder="请选择">-->
      <!--<el-option label="药品ID" value="1"></el-option>-->
      <!--<el-option label="药品名" value="2"></el-option>-->
      <!--</el-select>-->
      <!--按钮-->
      <!--slot=append是用来把按钮附着在输入框后-->
      <!--<el-button slot="append"  @click="searchMedicine(inputSearch)" icon="el-icon-search"></el-button>-->
    </el-input>
    <!--药品表-->
    <!--ref属性只是个标识，用来指代这个table-->
    <div align="center">
      <el-table
        ref="multipleTable"
        :data="tables.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @selection-change="handleSelectionChange">
        <!--reserve-selection="true"，可以保留选择状态-->
        <el-table-column
          type="selection"
          reserve-selection>
        </el-table-column>
        <el-table-column
          label="药品ID">
          <template slot-scope="scope">{{ scope.row.id }}</template>
        </el-table-column>
        <el-table-column
          prop="medName"
          label="药品名">
        </el-table-column>
        <el-table-column
          prop="description"
          label="描述"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="price"
          label="单价">
        </el-table-column>
        <el-table-column label="退库数量">
          <!--一列的计数器分别绑定表格数据,scope.row指的是
          当前行数据对象-->
          <template slot-scope="scope">
            <el-input-number
              size="mini"
              v-model="scope.row.returnNumbers"
              controls-position="right"
              @change="handleChange(scope.row)"
              :min="0"
              :max="200">
            </el-input-number>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--分页器 如何保留选中状态-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[1, 2, 3, 4]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="returnTableData.length">
    </el-pagination>
    <!--<el-button @click="toggleSelection([returnTableData[1], returnTableData[2]])">切换第二、第三行的选中状态</el-button>-->
    <el-button @click="handleSubmmit()">提交申请</el-button>
    <el-button @click="toggleSelection()">取消选择</el-button>
    <el-button @click="jumpTest()">返回</el-button>
  </div>
</template>

<script>
export default {
  name: 'ApplyMedicine',
  data() {
    return {
      // 没有这一行不会显示已经选择的
      // 可以设为el-select的value属性值
      // 就会默认显示对应的label
      select: '',
      // 让输入框默认是空的
      inputSearch: '',
      // 页面默认位置
      currentPage: 1,
      pagesize: 2,
      returnTableData: [{
        id: '11',
        price: '20.0',
        description: '色泽鲜艳,有剧毒',
        medName: '张三用',
        returnNumbers: '0'
      }, {
        id: '12',
        price: '250.0',
        description: '真香',
        medName: 'wjz',
        returnNumbers: '0'
      }, {
        id: '13',
        price: '50.0',
        description: '未知',
        medName: 'xxx',
        returnNumbers: '0'
      }],
      // 记录了选择的表项
      multipleSelection: []
    }
  },
  computed:{
    tables () {
      const search = this.inputSearch
      if (search) {
        // filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
        // 注意： filter() 不会对空数组进行检测。
        // 注意： filter() 不会改变原始数组。
        return this.returnTableData.filter(data => {
          // some() 方法用于检测数组中的元素是否满足指定条件;
          // some() 方法会依次执行数组的每个元素：
          // 如果有一个元素满足条件，则表达式返回true , 剩余的元素不会再执行检测;
          // 如果没有满足条件的元素，则返回false。
          // 注意： some() 不会对空数组进行检测。
          // 注意： some() 不会改变原始数组。
          return Object.keys(data).some(key => {
            // indexOf() 返回某个指定的字符在某个字符串中首次出现的位置，如果没有找到就返回-1；
            // 该方法对大小写敏感！所以之前需要toLowerCase()方法将所有查询到内容变为小写。
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.returnTableData
    },
  },
  methods: {
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val){
      this.multipleSelection = val;
    },
    handleSubmmit () {
      // console.log(this.applyTable);
      console.log(this.multipleSelection);
    },

    handleChange (row) {
      console.log(row.returnNumbers);
    },
    handleSizeChange (val) {
      this.pagesize = val;
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange (val) {
      this.currentPage = val;
      console.log(`当前页: ${val}`);
    },
    searchMedicine (input) {
      console.log(input);
    },
    jumpTest(){
      this.$router.push({path: '/调拨控制'})
    }
  }
}
</script>

<style scoped>
  /*搜索框的样式*/
  .el-input{
    width: 500px;
  }
  .el-select{
    width: 100px;
  }
  /*数据表的样式*/
  .el-table{
    width: 800px;
    height: 400px;
  }
</style>



